@include tablet {
  .contenedor-anuncios {
    @include grid(3, 2rem);

    //Cuando la pantalla sea mas grande que un telefono
    //se aplica este mixin de 3 columnas con separacion de 2rem
  }
}

.anuncio {
  border: 1px solid lighten($gris, 10);
  background-color: $gris;

  .contenido-anuncio {
    padding: 2rem;
  }
  h3,
  p {
    margin: 0;
  }
  picture {
    height: 27rem;
  }
}

.precio {
  color: $verde;
  font-weight: $bold;
  font-size: 3rem;
}
#galeria,
#galeria * {
  align-items: center;
  box-sizing: border-box;
}
#galeria {
  border: 1px solid #eaeaea; /* Borde de la galería */
  padding: 10px;
  padding-bottom: 0;
  background: white; /* Fondo de la galería */
  width: 100%;

  #galeria_miniaturas {
    display: table;
    margin: 0 auto;
  }
  #imgGaleria {
    border: 1px solid #f2f2f2; /* Borde de la imagen */
    padding: 3px;
    width: x; /* Ancho de la imagen */
    height: x; /* Alto de la imagen */
  }
  .miniatura {
    width: 10rem; /* Ancho de las miniaturas */
    height: 10rem; /* Alto de las miniaturas */
    float: left;
    cursor: pointer;
    padding: 5px;
    margin: 10px 5px;
  }
  .miniatura:hover {
    opacity: 0.8; /* Opacidad */
  }
  .miniatura:active {
    opacity: 1;
  } /* Ancho de la galería */
}

.conte-anuncios {
  h1 {
    font-size: 4rem;
    font-weight: $bold;
  }
}

.table-responsive {
  min-height: 0.01%;
  overflow-x: auto;
  text-align: center;
  table {
    img {
      width: 40rem;
      height: 25rem;
    }
  }
}

@media screen and (max-width: 767px) {
  .table-responsive {
    width: 100%;
    margin-bottom: 15px;
    overflow-y: hidden;
    -ms-overflow-style: -ms-autohiding-scrollbar;
    border: 1px solid #ddd;

    table {
      img {
        width: 100%;
        height: 30%;
        justify-content: center;
      }
    }
  }
  .table-responsive > .table {
    margin-bottom: 0;
  }
  .table-responsive > .table > thead > tr > th,
  .table-responsive > .table > tbody > tr > th,
  .table-responsive > .table > tfoot > tr > th,
  .table-responsive > .table > thead > tr > td,
  .table-responsive > .table > tbody > tr > td,
  .table-responsive > .table > tfoot > tr > td {
    white-space: nowrap;
  }
}
